/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.text {
  color: white;
  font: 500 1.6rem/165% Poppins, sans-serif;
}
.text-h2 {
  font-size: 2.8rem;
  line-height: 125%;
  letter-spacing: -0.01em;
}
@media (min-width: 64rem) {
  .text-h2 {
    font-size: 5rem;
    line-height: 110%;
  }
}
.text-h3 {
  font-weight: 400;
  line-height: 165%;
  letter-spacing: -0.02em;
}
@media (min-width: 64rem) {
  .text-h3 {
    line-height: 150%;
  }
}
.text-bold {
  font-weight: 700;
}
.text-input {
  color: #3e3c49;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: normal;
  letter-spacing: 0.01em;
}
@media (min-width: 64rem) {
  .text-input {
    letter-spacing: 0.03em;
  }
}
.text-secondary {
  color: #b9b6d3;
  font-weight: 400;
  font-size: 1.1rem;
  line-height: 195%;
}
.text-link {
  color: #ff7a7a;
  font-weight: 600;
}
.text-link:focus {
  outline: 0.1rem solid #3e3c49;
}
.text-error {
  color: #ff7a7a;
  font-size: 1.1rem;
  font-style: italic;
  line-height: 150%;
}

.paper {
  background: white;
  border-radius: 1rem;
  box-shadow: 0 0.8rem 0 rgba(0, 0, 0, 0.2);
}

.textInput {
  display: block;
}
.textInput_input {
  width: 100%;
  padding: 1.8rem 1.9rem 1.7rem;
  overflow: hidden;
  background: transparent;
  border: none;
  border-radius: 0.4rem;
  box-shadow: inset 0 0 0 0.1rem #b9b6d3;
  transition: 0.3s;
}
@media (min-width: 64rem) {
  .textInput_input {
    padding-right: 3.2rem;
    padding-left: 3.2rem;
  }
}
.textInput_input:focus {
  outline: none;
  box-shadow: inset 0 0 0 0.1rem #3e3c49;
}
.textInput_label {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.field {
  position: relative;
}
.field_errorIcon {
  position: absolute;
  top: 1.6rem;
  right: 1.6rem;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
}
@media (min-width: 64rem) {
  .field_errorIcon {
    right: 2.7rem;
  }
}
.field_feedback {
  max-height: 0;
  margin: 0;
  padding: 0;
  text-align: right;
  opacity: 0;
  transition: 0.3s;
}
.field-error .textInput_input {
  padding-right: 5.6rem;
  color: #ff7a7a;
  box-shadow: inset 0 0 0 0.2rem #ff7a7a;
}
@media (min-width: 64rem) {
  .field-error .textInput_input {
    padding-right: 7.8rem;
  }
}
.field-error .textInput_input:focus {
  box-shadow: inset 0 0 0 0.2rem #fa4c4c;
}
.field-error .field_errorIcon {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s, visibility 0s 0.3s;
}
.field-error .field_feedback {
  max-height: 3.2rem;
  padding-top: 0.6rem;
  visibility: visible;
  opacity: 1;
}

.button {
  padding: 1.7rem 3.2rem 1.1rem;
  overflow: hidden;
  color: white;
  font: 600 1.6rem/150% Poppins, sans-serif;
  letter-spacing: 0.03em;
  text-align: center;
  text-transform: uppercase;
  background: #38cc8c;
  border: none;
  border-bottom: 0.4rem solid #30bb7f;
  border-radius: 0.6rem;
  cursor: pointer;
  transition: 0.3s;
}
.button:focus {
  outline: none;
  box-shadow: 0 0 0 0.1rem #3e3c49;
}
.button:hover {
  background: #78e2b4;
  border-color: #63dea9;
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: url(bg-intro-mobile.png) center top, #ff7a7a;
}
@media (min-width: 64rem) {
  .wrapper {
    background: url(bg-intro-desktop.png) center top, #ff7a7a;
  }
}

.intro {
  max-width: 54rem;
  padding: 6.8rem 2.4rem;
}
@media (min-width: 64rem) {
  .intro {
    display: flex;
    max-width: 117rem;
    padding: 3rem;
  }
}

.header {
  padding: 2.4rem 0 3.9rem;
  text-align: center;
}
@media (min-width: 64rem) {
  .header {
    display: flex;
    flex: 1;
    flex-flow: column;
    justify-content: center;
    margin-right: 1.5rem;
    padding: 0;
    text-align: left;
  }
}
.header_heading {
  margin: 0;
}
@media (min-width: 64rem) {
  .header_heading {
    margin-top: 0.6rem;
  }
}
.header_text {
  margin: 2.4rem 0 0;
}
@media (min-width: 64rem) {
  .header_text {
    margin-top: 2.8rem;
  }
}

.signupForm {
  margin-top: 2.4rem;
}
@media (min-width: 64rem) {
  .signupForm {
    flex: 1;
    margin-top: 0;
    margin-left: 1.5rem;
  }
}
.signupForm_header {
  padding: 1.9rem 6.4rem 1.7rem;
  background: #6055a5;
}
.signupForm_heading {
  margin: 0;
  text-align: center;
}
.signupForm_body {
  margin-top: 2.4rem;
  padding: 2.4rem;
}
@media (min-width: 64rem) {
  .signupForm_body {
    padding: 4rem;
  }
}
.signupForm_fieldList {
  margin: 0;
  padding: 0;
  list-style: none;
}
.signupForm_field:not(:first-child) {
  margin-top: 1.6rem;
}
@media (min-width: 64rem) {
  .signupForm_field:not(:first-child) {
    margin-top: 2rem;
  }
}
.signupForm_submit {
  width: 100%;
  margin-top: 1.6rem;
}
@media (min-width: 64rem) {
  .signupForm_submit {
    margin-top: 2rem;
  }
}
.signupForm_agreement {
  margin: 0.8rem 0 0;
  padding: 0 1.6rem;
  text-align: center;
}
@media (min-width: 64rem) {
  .signupForm_agreement {
    margin-top: 1.2rem;
  }
}